<template>
  <div id="app">
    <sw-page-template class="page-wrapper" :impact-list="impactList" :impact-details="impactDetails">
      <template slot="content">
        <router-view />
      </template>
    </sw-page-template>
  </div>
</template>

<script>
import { AMZ } from './aws'
import { mockImpactList, mockImpactDetails } from './mocks'

import PageTemplate from '@/components/templates/page'

export default {
  name: 'App',
  data () {
    return {
      impactList: [],
      impactDetails: []
    }
  },
  created () {
    this.getImpactList()
    this.getImpactDetails()
  },
  methods: {
    getImpactList () {
      this.impactList = mockImpactList

      if (this.$store.getters.isLoggedIn) {
        AMZ.Lambda.fetch('getUserImpact').then(impact => {
          console.log('getUserImpact', impact)
        }, error => {
          console.error('getUserImpact', error)
        })
      }
    },
    getImpactDetails () {
      this.impactDetails = mockImpactDetails
    }
  },
  watch: {
    $route (route) {
      let $content = document.querySelector('.main-content')

      if (route.params && route.params.back) {
        // @TODO: scroll back item that user clicked on before hitting back button
      } else {
        $content.scrollTop = 0
      }
    }
  },
  components: {
    PageTemplate
  }
}
</script>

